  align-items: center;
  justify-content: flex-end;
  margin-top: 50px;
}
.tns-nav button {
  border-radius: 100%;
  width: 24px;
  height: 24px;
  background-color: var(--color-grey);
  border: none;
  color: var(--color-grey);
  margin-right: 5px;
  transition: width 0.5s, height 0.5s;
}
.tns-nav .tns-nav-active {
  background-color: white;
  border: 1px solid var(--color-blue);
  width: 60px;
  height: 60px;
  font-size: 32px;
  font-weight: 700;
  color: var(--color-grey);
  content: "01";
}

.slide__text {
  line-height: 1.3;
}
.slide__text h4 {
  margin-bottom: 10px;
}
.slide__text p {
  margin-bottom: 20px;
}
.slide__text ul,
.slide__text ol {
  margin-bottom: 20px;
  margin-top: 20px;
  margin-left: 50px;
}
.slide__text ul li,
.slide__text ol li {
  padding-bottom: 10px;
